* {
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  background-color: aliceblue;
}

.header {
  margin: auto -10px;
  padding: 10px 20px;
  box-shadow: 0 0 2px 0 rgb(114, 114, 114);
  display: flex;
  flex-direction: row;
  align-self: center;
  align-items: center;
}

.header h3 {
  font-size: 1.5rem;
  margin-left: 10px;
  color: #5f6368;
}

form {
  position: relative;
  width: 600px;
  margin: 32px auto 50px auto;
  background: #fff;
  padding: 7px;
  border-radius: 7px;
  box-shadow: 0 1px 7px rgb(128, 128, 128);
}

form input {
  width: 100%;
  border: none;
  padding: 4px 10px;
  margin-bottom: 20px;
  outline: none;
  font-size: 1rem;
  resize: none;
}

form textarea {
  width: 100%;
  border: none;
  padding: 4px 10px;
  outline: none;
  font-size: 1rem;
  resize: none;
}

.note {
  background: #fff;
  width: 240px;
  border-radius: 7px;
  box-shadow: 0 2px 5px rgb(175, 175, 175);
  padding: 10px;
  margin: 16px;
  float: left;
  margin-right: 0px;
}

.note h1 {
  font-size: 1 rem;
  margin-bottom: 6px;
}

.note p {
  font-size: 1rem;
  margin-bottom: 10px;
  white-space: pre-wrap;
  word-wrap: break-word;
}

form button {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 18px;
  bottom: -18px;
  background: rgb(255, 200, 18);
  color: #ffff;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  box-shadow: 0 1px 3px rgb(165, 165, 165);
  cursor: pointer;
  outline: none;
}

.note button {
  position: relative;
  float: right;
  color: rgb(128, 128, 128);
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}

.search {
  position: relative;
  width: 300px;
  margin: 32px auto 50px auto;
  background: #fff;
  padding: 7px;
  border-radius: 7px;
  box-shadow: 0 1px 7px rgb(128, 128, 128);
}

.search-box {
  border: none;
  padding: 4px 10px;
  margin-bottom: 20px;
  outline: none;
  font-size: 1rem;
  resize: none;
}

.search-btn {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 5px;
  background: rgb(255, 200, 18);
  color: #ffff;
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  box-shadow: 0 1px 3px rgb(165, 165, 165);
  cursor: pointer;
  outline: none;
}

@media (max-width: 767px) {
  .header {
    padding: 10px;
    margin: auto -5px;
  }

  .header h3 {
    font-size: 1.2rem;
  }

  form {
    width: 90%;
    margin: 32px auto 20px auto;
    padding: 7px;
  }

  form input,
  form textarea {
    padding: 3px 8px;
    font-size: 0.9rem;
  }

  .note {
    width: 100%;
    margin: 10px;
    padding: 8px;
  }

  .note h1,
  .note p {
    font-size: 0.9rem;
  }

  form button,
  .note button,
  .search-btn {
    width: 30px;
    height: 30px;
    font-size: 0.8rem;
  }

  .search {
    width: 90%;
    margin: 32px auto 20px auto;
    padding: 7px;
  }

  .search-box {
    padding: 3px 8px;
    font-size: 0.9rem;
  }
}

/* Tablet Styles */
@media (min-width: 768px) and (max-width: 1023px) {
  body {
    font-size: 18px;
  }

  /* Adjust other styles for tablet */
}
